import React, { Component } from 'react'
import HorizontalScrollBar from './HorizontalScrollBar';
import Lectures from './Lectures.js';
import './LL.css';
import { connect } from 'react-redux';

class LecturesList extends Component {
    componentDidMount() {
        this.props.getLectures();
    }
    render() {
        return (
            <div>
                <HorizontalScrollBar />
                {
                    this.props.lectures.map(lecture => (
                        (lecture.day === this.props.match.params.day) ? (
                            <Lectures key={lecture.day} lect={lecture.lect} />
                        ) : null
                    ))
                }
            </div>
        )
    }
}

const mapStatetoProps = (state) => ({
    lectures: state.lecture.lectures
})
const mapDispatchtoProps = (dispatch) => ({
    getLectures: () => {
        dispatch({ type: 'GET_LECTURES' })
    }

})

export default connect(mapStatetoProps, mapDispatchtoProps)(LecturesList);
