import React from 'react';
import './HS.css';
import { Link } from 'react-router-dom';

export default function HorizontalScrollBar() {
    return (
        <div>
            <div className="scrollmenu">
                <Link to={`/ForClassLec/${"mon"}`}>MONDAY</Link>
                <Link to={`/ForClassLec/${"tue"}`}>TUESDAY</Link>
                <Link to={`/ForClassLec/${"wed"}`}>WEDNESDAY</Link>
                <Link to={`/ForClassLec/${"thru"}`}>THRUSDAY</Link>
                <Link to={`/ForClassLec/${"fri"}`}>FRIDAY</Link>
                <Link to={`/ForClassLec/${"sat"}`}>SATURDAY</Link>
            </div>
        </div>
    )
}
