import React from 'react';
import { Link } from 'react-router-dom';

function ForClass() {
    return (
        <div>
            <div className="card" style={{padding:"2%"}}>
                <div className="card-body">
                    <h5 className="card-title">Lectures</h5>
                    <h6 className="card-subtitle mb-2 text-muted">check your time</h6>
                    <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                    <Link to={`/ForClassLec/${"mon"}`} className="card-link">Go</Link>
                </div>
            </div>
        </div>
    )
}
export default  ForClass;