import React from 'react';
import Lecture from './Lecture';
import './LL.css';

function Lectures(props) {
    return (
        <div>
            {
                props.lect.map(lecture=>(
                    <div key={lecture.id} className="lecture">
                        <Lecture time={lecture.time} subject={lecture.sub}/>
                    </div>
                ))
            }
        </div>
    )
}
export default  Lectures;
