import React from 'react';

function Lecture(props) {
    return (
        <div>
            <div className="card">
                <div className="card-header">
                    {props.time}
                </div>
                <div className="card-body">
                    <blockquote className="blockquote mb-0">
                        <p>{props.subject.toUpperCase()}<br /> Room no -</p>
                    </blockquote>
                </div>
            </div>
        </div>
    )
}

export default Lecture;
